// 验证模块
import Vue from 'vue'
import { Notify } from 'vant'
Vue.use(Notify);

class ValidForm {
    valid(o) {
        for (let key in o) {
            if (!o[key].reg.test(o[key].value)) {
                //vant 提醒组件
                Notify({
                    type: "warning",
                    message: o[key].errMsg
                })
                return false;
            }
        }
        return true;
    }

}

export const validForm = new ValidForm();