<template>
  <div class="login">
    <div class="login-box">
      <div class="topbox">
        <van-nav-bar>
          <template #left>
            <div class="leftbox">
              <div class="logo">
                <img src="../assets/img/logo2.png" alt />
              </div>
              <div class="textbox">KEKE Mall</div>
            </div>
          </template>
          <template #right>
            <div class="dian" @click="gohome">先去逛逛</div>
          </template>
        </van-nav-bar>
      </div>
      <div class="bodybox">
        <div class="bodytext-box">
          <h2>欢迎回来!</h2>
          <p>Please login to your accounts</p>
        </div>
        <div class="bodyinput-box">
          <van-form>
            <van-field v-model="userLoginInfo.phone" name="用户名/手机号码" placeholder="用户名/手机号码" />
            <van-field
              v-model="userLoginInfo.password"
              :type="isShowPw?'text':'password'"
              :right-icon="isShowPw?'eye-o':'closed-eye'"
              name="密码"
              placeholder="密码"
              @click-right-icon="showeye"
            />
            <div class="forgotbox fr" @click="goforgot">忘记密码?</div>
            <div class="submit-btn">
              <van-button
                round
                block
                color="#3a63f1"
                native-type="submit"
                @click="login"
              >登&nbsp;&nbsp;录</van-button>
            </div>
            <div class="login-btn">
              <van-button round block type="default" is-link @click="showPopup">注&nbsp;&nbsp;册</van-button>
              <van-popup
                v-model="popupShow"
                round
                position="bottom"
                closeable
                :style="{ height: '50%' }"
              >
                <div class="popupText">
                  <h2>注册</h2>
                  <van-form>
                    <van-field
                      v-model="registerInfo.nickName"
                      type="text"
                      name="用户名/昵称"
                      placeholder="用户名/昵称 用于取餐对您的称呼"
                    />
                    <van-field
                      v-model="registerInfo.phone"
                      name="手机号 中国(+86)"
                      placeholder="手机号 中国(+86)"
                    />
                    <van-field
                      v-model="registerInfo.password"
                      :type="ispopupShowPw?'text':'password'"
                      name="密码"
                      placeholder="密码"
                      :right-icon="ispopupShowPw?'eye-o':'closed-eye'"
                      @click-right-icon="popupshoweye"
                    />

                    <div style="margin-top: 16px;">
                      <van-button
                        round
                        block
                        color="#3a63f1"
                        native-type="submit"
                        @click="register"
                      >提&nbsp;&nbsp;交</van-button>
                    </div>
                  </van-form>
                </div>
              </van-popup>
            </div>
          </van-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/login.less";
import { utils } from "../assets/js/utils";
import { validForm } from "../assets/js/validForm";
export default {
  data() {
    return {
      userLoginInfo: {
        phone: "",
        password: "",
      },
      isShowPw: false,
      ispopupShowPw: false,
      popupShow: false,
      registerInfo: {
        nickName: "",
        phone: "",
        password: "",
      },
    };
  },
  methods: {
    showeye() {
      this.isShowPw = !this.isShowPw;
    },
    popupshoweye() {
      this.ispopupShowPw = !this.ispopupShowPw;
    },
    showPopup() {
      this.popupShow = !this.popupShow;
    },
    //注册
    register() {
      let registerInfo = this.registerInfo;
      let o = {
        phone: {
          value: registerInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errMsg: "手机号码格式不正确",
        },
        nickName: {
          value: registerInfo.nickName,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errMsg: "昵称格式错误，请输入2-11位的昵称",
        },
        password: {
          value: registerInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errMsg: "密码格式不正确，请输入以字母开头的6-16位密码",
        },
      };
      if (!validForm.valid(o)) {
        //验证不通过
        return;
      }
      // 加载提示
      this.$toast.loading({
        message: "加载中",
        //防止穿透
        forbidClick: true,
        duration: 0,
      });

      //序列化参数
      let info = Object.assign({}, registerInfo);
      info.appkey = this.appkey;
      let data = utils.queryString(info);

      // 访问
      this.axios({
        method: "POST",
        url: "/register",
        data,
      })
        .then((result) => {
          // 关闭加载
          this.$toast.clear();
          if (result.data.code == 100) {
            this.popupShow = false;
            this.$toast(result.data.msg);
            // 清空表单数据
            for (let key in registerInfo) {
              registerInfo[key] = "";
            }
          } else {
            this.$toast({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {});
    },

    //登录
    login() {
      let logininfo = this.userLoginInfo;
      let o = {
        phone: {
          value: logininfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errMsg: "手机号码格式不正确",
        },
        password: {
          value: logininfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errMsg: "密码格式不正确，请输入以字母开头的6-16位密码",
        },
      };
      if (!validForm.valid(o)) {
        //验证不通过
        return;
      }
      // 加载提示
      this.$toast.loading({
        message: "加载中",
        //防止穿透
        forbidClick: true,
        duration: 0,
      });

      //序列化参数
      let info = Object.assign({}, logininfo);
      info.appkey = this.appkey;
      let data = utils.queryString(info);

      // 访问
      this.axios({
        method: "POST",
        url: "/login",
        data,
      })
        .then((result) => {
          // 关闭加载
          this.$toast.clear();
          if (result.data.code == 200) {
            // 清空表单数据
            for (let key in logininfo) {
              logininfo[key] = "";
            }
            localStorage.setItem("CSTK", result.data.token);
            this.$router.push({ name: "Home" });
          } else {
            this.$toast({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {});
    },
    //去主页
    gohome() {
      this.$router.push("/home");
    },
    //忘记密码
    goforgot() {
      this.$router.push("/forgotpw");
    },
  },
};
</script>